body {
  background-image: url("assets/background.png") !important;
  background-repeat: repeat-y;
  background-size: cover;
  font-family: "Poppins", "Courier New", Courier, monospace;
  margin: 0;
}

.video-container {
  height: 100vh;
  display: block;
  position: relative;
}

.video-container > video {
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: 0;
}

.video-container .message {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}

.play {
  background-image: url(assets/pngkit_play-png_4474.png);
  background-size: cover;
  cursor: pointer;
  opacity: 0.1;
  height: 250px;
  width: 250px;
}
